.contact-us {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: $colorWhite;
  }

  &__info-link {
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: $colorWhite;
    text-decoration: none;

    &:hover {
      color: #a19fa1;
    }
  }

  &__field {
    margin: 0 0 16px;
  }
}
